.nav {
    &__list {
        display: none;
        @include mq($medium) {
            display: flex; } }

    &__trigger {
        @include resetbutton();
        @include mq($medium) {
            display: none; } }

    &__icon_open {
        display: block;
        @include tr(.3s ease); }

    &__icon_close {
        display: none;
        @include tr(.3s ease); }

    // &__icon_close
    //     display: none
    //     opacity: 0
    //     +tr(opacity .3s ease)

    &__trigger_active {
        .nav__icon_open {
            display: none;
            @include tr(.3s ease); }
        .nav__icon_close {
            display: block;
            @include tr(.3s ease); }
        & + .nav__list {
                position: absolute;
                top: 100%;
                left: 0;
                right: 0;
                padding-top: 54px;
                padding-bottom: 10px;
                padding-left: 40px;
                padding-right: 40px;
                display: block;
                background: #fff;
                // animation: slide-in-bck-cente 0.1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both
                .nav__item {
                    margin-bottom: 40px;
                    margin-right: 0; } } }


    &__item {
        margin-right: 40px;
        &:last-child {
            margin-right: 0; } }
    &__link {
        position: relative;
        color: #1b1e64;
        font-size: 18px;
        font-weight: 500;
        text-decoration: none;
        box-shadow: inset 0 -1px 0 transparent;
        @include tr( .3s cubic-bezier(.4,0,0,1));
        &:hover {
            color: lighten(#1b1e64, 10);
            box-shadow: inset 0 -1px 0 lighten(#1b1e64, 10);
            @include tr( .3s cubic-bezier(.4,0,0,1)); } } }


