.nav-sec_second {

    .nav-sec__item {
        margin-bottom: 6px;
        max-width: 100px;
        min-width: 100px;
        @include mq($medium) {
            margin-bottom: 30px;
            text-align: right; }
        @include mq($large) {
            margin-bottom: 12px;
            text-align: left; }

        &:nth-child(3) {
            display: block; } }

    .nav-sec__link {
        color: #eaece9;
        font-size: 12px;
        font-weight: 400;
        line-height: 1;
        @include mq($medium) {
            color: #eaece9;
            font-size: 14px;
            line-height: 1.071; }


        @include mq($large) {
            color: #ffffff;
            font-size: 14px;
            line-height: 1.071; }

        &:hover {
            color: darken(#ffffff, 10);
            @include mq($medium) {
                box-shadow: inset 0 -1px 0 darken(#eaece9, 10); } } } }
