.footer {
  background-color: #1b1e64;
  overflow: hidden;

  &__container {
    padding-top: 60px;
    padding-bottom: 60px;
    flex-wrap: wrap;
    @include mq($medium) {
      padding-top: 40px;
      padding-bottom: 40px; } }

  &__row {
    flex-wrap: wrap;
    @include mq($medium) {
      flex-wrap: nowrap; } }


  &__col {
    width: 100%;
    &:nth-child(1) {
      order: 1;
      max-width: 190px;
      @include mq($medium) {
        max-width: 258px; }

      @include mq($large) {
        order: 0;
        max-width: 360px; } }
    &:nth-child(2) {
      order: 0;
      max-width: 100%;
      @include mq($medium) {
        max-width: 290px;
        order: 1; }
      @include mq($large) {
        max-width: 490px; } }

    &:nth-child(3) {
      order: 2;
      margin-left: auto;
      margin-right: -21px;
      max-width: 100px;
      @include mq($medium) {
        order: 2;
        margin-right: 0; } } }

  &__nav {
    padding-top: 2px; }

  &__nav-sec {
    padding-top: 4px;
 }    // margin-right: -20px

  &__copyright {
    margin-top: 40px;
    color: rgba(255, 255, 255, 0.6);
    font-weight: 400;
    font-size: 14px;
    line-height: 1.071;
    @include mq($medium) {
      margin-top: -50px; }
    @include mq($large) {
      margin-top: 0;
      font-size: 16px; } } }
