.mission {
    position: relative;
    background-position: 50% 0;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    &__container {
        position: relative;
        z-index: 1;
        padding-top: 40px;
        padding-bottom: 39px;
        @include mq($medium) {
            padding-top: 60px; } }
    &__w {
        max-width: 600px;
        width: 100%; }
    &__title {
        margin-bottom: 25px;
        @include mq($medium) {
            margin-bottom: 32px; } }
    // &__text

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } } }
